window.admin.init = {

    csrfFieldAttribute: () => {
        let fields = document.querySelectorAll('input[type="hidden"]');
        fields.forEach(function(field) {
            field.setAttribute('hidden', true);
        });
    },

    initNotify: () => {
        // 
        Noty.overrideDefaults({
            layout: 'topRight',
            theme: 'sunset'
        });
    }

};
